<template>
  <div style="display: none">
    <router-link id="home-link" to="/"></router-link>
    <router-link id="gallery-link" to="/gallery"></router-link>
    <img src="./assets/projects/book.jpg" />
    <img src="./assets/projects/neptone.jpg" />
    <img src="./assets/projects/portfolioV3.jpg" />
    <img src="./assets/projects/projectZ.jpg" />
    <img src="./assets/projects/satofill.jpg" />
    <img src="./assets/projects/swiast.jpg" />
    <img src="./assets/projects/mmm.png" />
    <img src="./assets/projects/m-labs.png" />
  </div>
  <transition name="fade-back">
    <m-button v-if="showback" class="back">
      <router-link to="/">
        <i class="fas fa-arrow-left"></i>
      </router-link>
    </m-button>
    <m-button v-else @click="changeTheme" class="theme-switch">
      <transition name="slide-2-bottom" mode="out-in">
        <i class="fas fa-sun" v-if="activeTheme === 'm-dark'"></i>
        <i class="fas fa-moon" v-else></i>
      </transition>
    </m-button>
  </transition>
  <router-view v-slot="{ Component }">
    <transition name="fade-back" mode="out-in">
      <component :is="Component" />
    </transition>
  </router-view>
  <transition name="fade-back">
    <p-loading v-if="isLoading" @finished-loading="isLoading = false" />
  </transition>
</template>

<script>
import { setTheme, setDefaultTheme, configUI } from "m-labs";
import PLoading from "./components/PLoading.vue";
export default {
  name: "app",
  components: { PLoading },
  watch: {
    $route(newRoute) {
      if (newRoute.path.length < 2) {
        this.showback = false;
      } else {
        this.showback = true;
      }
      if (newRoute.path === "/gallery") {
        document.getElementById("home-link").click();
        setTimeout(document.getElementById("gallery-link").click(), 1000);
      }
    },
    activeTheme(newTheme) {
      document.body.classList = [newTheme];
      localStorage.setItem("activeTheme", newTheme);
      console.log(
        newTheme,
        localStorage.getItem("activeTheme"),
        this.activeTheme
      );
    },
  },
  data() {
    return {
      isLoading: true,
      showback: false,
      activeTheme: "",
    };
  },
  methods: {
    changeTheme() {
      if (this.activeTheme === "") {
        this.activeTheme = "m-dark";
      } else {
        this.activeTheme = "";
      }
    },
  },
  mounted() {
    this.activeTheme = localStorage.getItem("activeTheme");
    setDefaultTheme({
      main: "rgba(250, 250, 246, 1)",
      text: "rgba(41, 45, 100, 1)",
      accent: "rgba(235, 101, 104, 1)",
      onAccent: "rgba(255, 255, 255, 1)",
    });
    setTheme("dark", {
      text: "rgba(250, 250, 246, 1)",
      main: "rgba(41, 45, 100, 1)",
      accent: "rgba(235, 101, 104, 1)",
      onAccent: "rgba(255, 255, 255, 1)",
    });
    configUI({ en: "Poppins", ar: "Readex Pro" }, "6px");
    document.body.classList.add(this.mountedTheme);
  },
};
</script>

